*{
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

p{
  font-size: 1.6rem;
}

h1{
  font-size: 6rem;
}

.app{
  width: 100%;
  height: 100%;
  position: relative;
  background-color: rgba(0, 0, 0, 0.4);
  color: #fff;
}

.app::before{
  content: "";
  background: url("./asset/sunset.jpg") no-repeat center center/cover;
  position: absolute;
  width: 100%;
  height: 100vh;
  padding-bottom: 2rem;
  top: 0;
  left: 0;
  z-index: -1;
}

.Clouds{
  width: 100%;
  height: 100%;
  position: relative;
  background-color: rgba(0, 0, 0, 0.4);
  color: #fff;
}

.Clouds::before{
  content: "";
  background: url("./asset/cloudy.jpg") no-repeat center center/cover;
  position: absolute;
  width: 100%;
  height: 100vh;
  padding-bottom: 2rem;
  top: 0;
  left: 0;
  z-index: -1;
}

.Rain{
  width: 100%;
  height: 100%;
  position: relative;
  background-color: rgba(0, 0, 0, 0.4);
  color: #fff;
}

.Rain::before{
  content: "";
  background: url("./asset/rainy4.jpg") no-repeat center center/cover;
  position: absolute;
  width: 100%;
  height: 100vh;
  padding-bottom: 2rem;
  top: 0;
  left: 0;
  z-index: -1;
}


.Clear{
  width: 100%;
  height: 100%;
  position: relative;
  background-color: rgba(0, 0, 0, 0.4);
  color: #fff;
}

.Clear::before{
  content: "";
  background: url("./asset/sunny1.webp") no-repeat center center/cover;
  position: absolute;
  width: 100%;
  height: 100vh;
  padding-bottom: 2rem;
  top: 0;
  left: 0;
  z-index: -1;
}

.search{
  text-align: center;
  padding: 1rem;
}

input{
  padding: .7rem 1.5rem;
  font-size: 1.2rem;
  border-radius: 25px;
  border: 1px solid rgba(255, 255, 255, 0.8);
  background-color: rgba(255, 255, 255, 0.2);
  color: #fff;
}

::placeholder{
  color: #f8f8f8;
}

.container{
  max-width: 700px;
  height: 92vh;
  margin: auto;
  padding: 1rem;
  padding-bottom: 5%;
  position: relative;
  top: 10%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.app .top{
  width: 100%;
  margin: 1rem auto;
}

.description{
  position: relative;
  right: -90%;
  transform-origin: 0 0;
  transform: rotate(270deg);
}

@media(max-width: 2000px){
  .bottom{
    display: flex;
    width: 100%;
    justify-content: space-evenly;
    text-align: center;
    width: 100%;
    margin: 1rem auto;
    padding: 1rem;
    border-radius: 12px;
    background-color: rgba(255, 255, 255, 0.2);
  }
  
  .bold{
    font-weight: 700;
  }
}

@media(max-width: 600px){
  .bottom{
    display: flex;
    width: 100%;
    justify-content: space-evenly;
    text-align: center;
    width: 100%;
    margin: 4rem auto;
    padding: 1rem;
    border-radius: 12px;
    background-color: rgba(255, 255, 255, 0.2);
  }
  
  .bold{
    font-weight: 700;
    font-size: 16px;
  }

  .description-p{
    font-size: 16px;
  }
}